<template>
  <div id="app">
    <header class="header-container">
      <div>统考练习系统</div>
      <div class="home" @click="routerHome"> <i class="el-icon-s-home"></i> 首页</div>
    </header>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods:{
    routerHome(){
      this.$router.push({name:'classify'})
    }
  }
}
</script>

<style>
.header-container{
  height: 40px;
  background: #2f5b88;
  color: #fff;
  display: flex;
  padding: 0px 60px;
  justify-content: space-between;
  align-items: center;
}
*{
  box-sizing: border-box;
}
html,body,#app{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#app {
  width: 100vw;
  height: 100vh;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.home{
  cursor: pointer;
}

</style>



