import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import classify from '@/page/classify'
import jiaojiyongyv from '@/page/jiaojiyongyv'
import yuedu1 from '@/page/yuedu1'
import yuedu2 from '@/page/yuedu2'
import cihuei from '@/page/cihuei'
import wanxing from '@/page/wanxing'
import fanyi from '@/page/fanyi'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'classify',
      component: classify
    },
    {
      path: '/jiaojiyongyv',
      name: 'jiaojiyongyv',
      component: jiaojiyongyv
    },
    {
      path: '/yuedu1',
      name: 'yuedu1',
      component: yuedu1
    },
    {
      path: '/yuedu2',
      name: 'yuedu2',
      component: yuedu2
    },
    {
      path: '/cihuei',
      name: 'cihuei',
      component: cihuei
    },
    {
      path: '/wanxing',
      name: 'wanxing',
      component: wanxing
    },
    {
      path: '/fanyi',
      name: 'fanyi',
      component: fanyi
    },
    
  ]
})



